import { BpmnModel } from "../BpmnModel";
import { BpmnModelHelper } from "../BpmnModelHelper";
import { IClrTypeElement } from "../IClrTypeElement";
import { OmgdiElement } from "./OmgdiElement";

export abstract class DiagramElement extends OmgdiElement implements IClrTypeElement {
    constructor(model: BpmnModel) {
        super(model);
    }

    abstract ClrTypeID: string;
    abstract NewInstance(): DiagramElement;

    ConvertToJsonObject(): any {
        let obj = super.ConvertToJsonObject();

        BpmnModelHelper.SetAttributeValue(obj, "ClrTypeID", this.ClrTypeID);

        let extensionElementsObj = {};
        this.WriteExtensionElements(extensionElementsObj);
        if (Object.getOwnPropertyNames(extensionElementsObj).length > 0) {
            BpmnModelHelper.SetAttributeValue(obj, "extensionElements", extensionElementsObj);
        }

        return obj;
    }

    LoadFromJsonObject(obj: any): void {
        super.LoadFromJsonObject(obj);

        if (obj.hasOwnProperty("extensionElements")) {
            this.LoadExtensionElements(obj.extensionElements);
        }
    }

    LoadExtensionElements(extensionElementsObj: any): void {
    }

    WriteExtensionElements(extensionElementsObj: any): void {
    }
}